*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    margin-bottom: 10px;
}
body{
    background: rgb(131,58,180);
    background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
}
.recipe{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.row{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    margin-top: 20px;
}

input[type='text']{
    width: 40%
}
.btn{
    width: 20%;
}

